.contactMe {
    padding: 50px;
}

.contactMe h1, .contactMe p {
    text-align: center;
}

.contactIcons {
    list-style: none;
    display: flex;
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 10px;
}

.contactIcons a {
    margin-right: 10px;
}

.linkedin-icon {
    fill: #0077B5;
    width: 35px;
    height: 35px;
}

.icon {
    fill: black;
    width: 35px;
    height: 35px;
}

a:visited, a:hover, a:link {
    color: black;
}

.footer {
    text-align: center;
    padding: 20px;
}