.about {
    margin: 0;
    padding: 0;
    background-color: rgb(230, 230, 230);
}

.hero {
    background-size: cover;
    box-shadow: inset 0 0 0 2000px rgba(0, 0, 0, 0.6);
    display: flex;
    height: 100vh;
}

.content {
    display: flex;
    flex-direction: column;
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: white;
}

.content h1 {
    font-size: 56px;
    font-weight: bolder;
}

.content p {
    font-size: 22px;
    margin: 0;
    padding-bottom: 10px;
    max-width: 550px;
    letter-spacing: 2px;
}

.content a {
    color: inherit;
    text-decoration: none;
    background-color: rgb(102, 204, 255, 0.75);
    padding: 12px 24px;
    font-size: 22px;
    border-radius: 10px;
    margin: 20px 0;
}

.content a:hover {
    background-color: rgb(102, 204, 255, 1);
}

.bio {
    padding: 50px;
    font-size: 20px;
}

.title {
    padding: 0 50px;
}

/* Technologies Section */

.tech-sec {
    padding: 50px 0;
    margin: 0 auto;
}

.tech-sec h2 {
    text-align: center;
}

.tech {
    display: flex;
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding-bottom: 50px;
}

.tech li{
    list-style: none;
    padding: 10px;
    width: 50px;
    height: 50px;
    fill: black;
}

/* Hover Transitions */

.html-icon:hover {
    fill: #f06529;
    transition: fill 0.2s ease-in-out;
}
.css-icon:hover {
    fill: #2965f1;
    transition: fill 0.2s ease-in-out;
}
.js-icon:hover {
    fill: #e4cf4b;
    transition: fill 0.2s ease-in-out;
}
.react-icon:hover {
    fill: #61dbfb;
    transition: fill 0.2s ease-in-out;
}
.node-icon:hover {
    fill: #68a063;
    transition: fill 0.2s ease-in-out;
}
.python-icon:hover {
    fill: #4584b6;
    transition: fill 0.2s ease-in-out;
}
.aws-icon:hover {
    fill: #ff7f0e;
    transition: fill 0.2s ease-in-out;
}

/* Work Experience Section */

.jobs {
    display: grid;
    grid-template-columns: repeat(3, 1fr); /* 3 equal columns */
}

.tile {
    position: relative;
    text-align: center;
    color: white;
    height: 250px;
}

.tile img:hover {
    filter: unset;
}

.tile img {
    width: 100%;
    height: 250px;
    object-fit: cover;
    margin-bottom: 10px;
    filter: brightness(50%);
}

.tile h2 {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}